//import icons
import svgIcon from '../../assets/images/icons.svg';

//Views Parent Class
export default class View {
  //parent element
  _parentEl = '';
  //Data
  _data;

  // Error
  _errorMessage = '';

  _message = '';

  //pagination defaults
  pageNo;
  totalRecipes;

  _clear() {
    this._parentEl.innerHTML = '';
  }

  renderError(message = this._errorMessage) {
    const markup = `
        <div class="error">
            <div>
            <svg>
                <use href="${svgIcon}#icon-alert-triangle"></use>
            </svg>
            </div>
            <p>${message}</p>
        </div>
      `;

    this._clear();
    this._parentEl.insertAdjacentHTML('afterbegin', markup);
  }

  renderMessage(message = this._message) {
    const markup = `
        <div class="message">
            <div>
            <svg>
                <use href="${svgIcon}#icon-smile"></use>
            </svg>
            </div>
            <p>${message}</p>
        </div>
      `;

    this._clear();
    this._parentEl.insertAdjacentHTML('afterbegin', markup);
  }

  renderSpinner() {
    const markup = `
        <div class="spinner">
          <svg>
            <use href="${svgIcon}#icon-loader"></use>
          </svg>
        </div> 
    `;
    this._clear();
    this._parentEl.insertAdjacentHTML('afterbegin', markup);
  }

  render(data) {
    if (!data || (Array.isArray(data) && data.length === 0))
      return this.renderError();

    this._data = data;

    const markup = this._generateMarkup();

    this._clear();
    this._parentEl.insertAdjacentHTML('afterbegin', markup);
  }

  update(data) {
    if (!data || (Array.isArray(data) && data.length === 0)) return;

    this._data = data;

    const newMarkup = this._generateMarkup();

    const newDOM = document.createRange().createContextualFragment(newMarkup);

    const newElements = Array.from(newDOM.querySelectorAll('*'));

    const curElements = Array.from(this._parentEl.querySelectorAll('*'));

    //Handle error if there is manipulatation of HTML elements(add or reduction)
    if (newElements.length !== curElements.length) {
      return this.renderMessage(`Internal Error. Can't process your request`);
    }

    newElements.forEach((newEl, i) => {
      const curEl = curElements[i];

      //update changed text
      if (
        !newEl.isEqualNode(curEl) &&
        newEl.firstChild?.nodeValue.trim() !== ''
      ) {
        curEl.textContent = newEl.textContent;
      }

      //update changed attributes
      if (!newEl.isEqualNode(curEl)) {
        Array.from(newEl.attributes).forEach(attr =>
          curEl.setAttribute(attr.name, attr.value)
        );
      }
    });
  }

  getPaginationInfo(pageNo = 0, totalRecipes = 0) {
    this.pageNo = pageNo;
    this.totalRecipes = totalRecipes;
  }
}
